/** @jsx React.DOM */
import {Route, DefaultRoute} from 'react-router';
import React from 'react';

import Placeholder from '../components/__placeholder__';
import SourceMap from '../components/source-map';
import App from '../components/app';
import NewSourceMap from '../components/source-map/new';


module.exports = (
/* jshint ignore:start */
    React.createElement(Route, {handler: App}, 
      React.createElement(DefaultRoute, {name: "index", handler: Placeholder}), 
      React.createElement(Route, {name: "sourceMap", handler: SourceMap}, 
        React.createElement(Route, {name: "new", path: "/upload", handler: NewSourceMap}), 
        React.createElement(Route, {name: "show", path: "/:path", handler: Placeholder})
      ), 
      React.createElement(Route, {name: "sourceLocation", path: "/:path/:line,:column", handler: Placeholder})
    )
/* jshint ignore:end */
);




