import React from 'react';

export default React.createClass({
    getInitialState() {
        return {
            lineNumber: 0,
            columnNumber: 0,
            originalLocation: null
        };
    },
    setLineNumber(e) {
        this.setState({ lineNumber: e.target.value });
    },
    setColumnNumber(e) {
        this.setState({ columnNumber: e.target.value });
    },
    calculateLine() {
        console.log('calculating from', this.state);
        let location =
            this.props.details.originalPositionFor({
                line: parseInt(this.state.lineNumber),
                column: parseInt(this.state.columnNumber)
            });
        console.log('got:', location);
        this.setState({
            originalLocation: location
        });
    },
    render() {
        var parsedMap = this.props.details;
        var originalLocation;

        if (this.state.originalLocation !== null) {
            let location = this.state.originalLocation;
            let identifier;

            if (location.name !== null) {
                /* jshint ignore:start */
                identifier = (React.createElement("p", null, "Referring to \"", location.name, "\""));
                /* jshint ignore:end */
            }

            originalLocation = (
                /* jshint ignore:start */
                React.createElement("section", null, 
                    "You can find what you're looking for at:", 
                    React.createElement("p", null, location.source), 
                    React.createElement("p", null, "Line ", location.line, ", Column ", location.column), 
                    identifier
                )
                /* jshint ignore:end */
            );
        }

        return (
/* jshint ignore:start */
            React.createElement("div", null, 
                React.createElement("h3", null, "Sourcemap for:"), 
                React.createElement("section", null, 
                    React.createElement("p", null, parsedMap.file), 
                    React.createElement("p", null, "Version: ", parsedMap.version)
                ), 
                React.createElement("section", null, 
                    React.createElement("h4", null, "Lookup Line Number"), 
                    React.createElement("label", null, 
                        "Line:", 
                        React.createElement("input", {type: "number", onChange: this.setLineNumber})
                    ), 
                    React.createElement("label", null, 
                        "Column:", 
                        React.createElement("input", {type: "number", onChange: this.setColumnNumber})
                    ), 
                    React.createElement("button", {onClick: this.calculateLine}, "Get Line Number")
                ), 
                React.createElement("section", null, 
                    originalLocation
                )
            )
/* jshint ignore:end */
        );
    }
});



